#searchLayer{
    position: absolute;
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    grid-template-rows: 1fr 2fr 1fr;
    grid-template-areas: 
    "one two three"
    "four five six"
    "seven eight nine";
    pointer-events: none;
}

#five{
    justify-self: center;
    -ms-flex-item-align: center;
        align-self: center;
    grid-row-start: 2;
    grid-column-start: 2;
    pointer-events: all;

    background-color: rgba(255,255,255,.4);
}



